#navbar {
  padding: 0 0 10px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

}

.nav-links {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 30px;
}

.outlined-button {
  padding: 8px 20px;
  border: 1px solid var(--black);
  border-radius: 50px;
  background-color: transparent;
  cursor: pointer;
  transition: background 0.2s;
}

.outlined-button:hover {
  background-color: hsl(0, 0%, 0%, 0.05);
}