@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600&display=swap);
* {
  font-family: "Inter", Arial, Helvetica, sans-serif;
}

:root {
  --blue: #3c68ff;
  --white: #fff;
  --light-grey: #fdfdfd;
  --grey: #f5f5f5;
  --mid-grey: #f2f2f2;
  --dark-blue: #2c2b3c;
  --black: #000;
}

body {
  overflow-y: scroll;
  margin: 0;
  padding: 12px 24px 100px 24px;
  background-color: #fdfdfd;
  background-color: var(--light-grey);
}

.max-width {
  max-width: 1920px;
}

h1 {
  color: #000;
  color: var(--black);
  margin: 0;
  font-size: 60px;
}

h2 {
  color: #000;
  color: var(--black);
  margin: 0;
  font-size: 30px;
}

h3 {
  color: #000;
  color: var(--black);
  margin: 0;
  font-size: 24px;
}

h4 {
  color: #000;
  color: var(--black);
  margin: 0;
  font-size: 18px;
}

h5 {
  color: #000;
  color: var(--black);
  margin: 0;
  font-size: 16px;
  font-weight: 500;
}

p {
  color: #000;
  color: var(--black);
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

a:link,
a:visited,
a:active {
  cursor: pointer;
  color: #000;
  color: var(--black);
  text-decoration: none;
  transition: all 0.2s;
}

a:hover {
  outline: none;
  color: #2c2b3c;
  color: var(--dark-blue);
}

a:focus-visible {
  outline: 2px solid #3c68ff;
  outline: 2px solid var(--blue);
}

.primary-btn {
  font-size: 16px;
  color: #fff;
  background-color: #3c68ff;
  background-color: var(--blue);
  padding: 10px 20px;
  width: 280px;
  border-radius: 50px;
  border: solid 1px transparent;
  transition: background-color 0.3s, box-shadow 0.2s;
}

.primary-btn:hover {
  cursor: pointer;
  background-color: rgb(26, 115, 232, 0.95);
}

.primary-btn:focus-visible {
  border: solid 1px #1A73E8;
  box-shadow: 0 0 0 2px rgba(39, 94, 254, .3);
  outline: none;
}

.primary-btn:disabled {
  color: rgb(44, 43, 60, 0.4);
  background-color: rgb(44, 43, 60, 0.03);
}

.primary-btn:disabled:hover {
  cursor: not-allowed;
  background-color: rgb(44, 43, 60, 0.03);
}


.input-label {
  color: #59586A;
  font-size: 16px;
  padding-bottom: 10px;
  display: block;
  width: 100%;
}

.input {
  box-sizing: border-box;
  caret-color: hsl(0, 0%, 0%, 0.5);
  background: #fff;
  background: var(--white);
  border: solid 1px hsl(0, 0%, 0%, 0.2);
  border-radius: 6px;
  color: hsl(0, 0%, 0%, 0.5);
  font-size: 16px;
  font-weight: 500;
  padding: 14px 16px;
  width: 100%;
  transition: border 0.2s, box-shadow 0.2s;
}

.input:hover {
  border: solid 1px #1A73E8;
}

.input:focus-visible {
  outline: none;
  border: solid 1px #1A73E8;
  box-shadow: 0 0 0 2px rgba(39, 94, 254, .3);
}

.input:invalid {
  border: solid 1px rgb(232, 26, 26);
}

.input:invalid:focus {
  box-shadow: 0 0 0 2px rgba(232, 39, 39, 0.3);
}

.dialog {
  margin: 50px auto;
  background-color: #f2f2f2;
  background-color: var(--mid-grey);
  border-radius: 20px;
  padding: 40px 24px;
  max-width: 380px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
}

.dialog div {
  width: 100%;
}

.dialog button {
  margin-bottom: 20px;
}

.dialog h3 {
  text-align: left;
  margin-bottom: 20px;
}

#navbar {
  padding: 0 0 10px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

}

.nav-links {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
}

.outlined-button {
  padding: 8px 20px;
  border: 1px solid var(--black);
  border-radius: 50px;
  background-color: transparent;
  cursor: pointer;
  transition: background 0.2s;
}

.outlined-button:hover {
  background-color: hsl(0, 0%, 0%, 0.05);
}
.form input:first-child {
    background-color: red;
    margin-bottom: 20px;
}
table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border-radius: 10px;
    border-collapse: collapse;
    overflow: hidden;
    border: 1px solid transparent;
}
  
td, th {
    border: 1px solid transparent;
    text-align: left;
    padding: 8px;
}
  
tr:first-child {
    background-color: hsl(226, 100%, 62%, 0.1);
    outline: 1px solid var(--blue)
}

tr {
    padding: 10px;
}

tr:nth-child(even) {
    background-color: #dddddd;
}

.dialog {
    padding-top: 20px;
}

.recording-none {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.recording-none p {
    margin-bottom: 15px;
}

.recording-none h4 {
    margin-bottom: 5px;
}
#display-name {
  margin-bottom: 20px
}
.feature-wrapper {
    max-width: 1200px;
    margin: auto;
}

.title {
    margin-bottom: 20px;
}

.row-cards {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 280px;
    padding: 20px;
    background-image: linear-gradient(hsl(303, 25%, 55%, 0.4), hsl(252, 81%, 63%, 0.4));
    border-radius: 20px;
}

.purple {
    background-image: linear-gradient(hsl(303, 25%, 55%, 0.4), hsl(252, 81%, 63%, 0.4));
}

.blue {
    background-image: linear-gradient(hsl(200, 99%, 65%, 0.4), hsl(226, 100%, 62%, 0.4));
}

.yellow {
    background-image: linear-gradient(hsl(49, 100%, 50%, 0.4), hsl(35, 93%, 49%, 0.4));
}

.card img {
    margin: auto;
}

.card h2, .long-card h2 {
    margin-bottom: 10px;
}

.card p {
    margin-bottom: 60px;
}

.long-card {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    max-width: 1200px;
    padding: 20px 20px 20px 70px;
    background-image: linear-gradient(hsl(95, 86%, 38%, 0.4), hsl(119, 77%, 28%, 0.4));
    border-radius: 20px;
}

.long-card div {
    max-width: 550px;
}

.long-card img {
    height: 150px;
}

.long-card img:first-child {
    margin-right: -90px;
}

.footer {
    margin-top: 150px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.credits {
    text-align: right;
}
#hero-banner {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  margin: 24px;
  height: calc(100vh - 180px);
}

.hero-img {
  height: 100%;
}

.hero-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  max-width: 550px;
  height: 100%;
}

#together {
  margin-top: -10px;
}

#hero-button {
  max-width: 280px;
  padding: 14px 20px;
  border-radius: 50px;
  border: none;
  background-color: var(--black);
  color: var(--white);
  font-size: 18px;
  cursor: pointer;
  margin-bottom: 100px;
}

